
import XHeader from 'vux/src/components/x-header/index.vue'

import showMore from '@/components/ShowMore/showMore'  
export default {
components:{
XHeader,
showMore
},
data() {
return {
id: "",
info: {},
}
},
mounted() {
this.getParams();
},
methods: {

getParams() {
var routerParams = this.$route.params;
this.id = routerParams.ID;
this.getDetail();
},

getDetail() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "suifangService",
"MSH.2": "getCHFSById",
"id": this.id,
'token':this.$root.loginInfo.token,
}).then(res => {
this.$vux.loading.hide();
console.log(res);
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
}



